
export default class Environement {
    
    width = 0;
    height = 0;
    content = [];
    initialCellState = false;
    nbOfcellsAliveNearMe = 0;

    constructor(width,height,initialCellState) {
        this.width = width;
        this.height = height;
        this.initialCellState = initialCellState;
        this.content = this.initialise();
    }
    
    initialise(){
        let ih = 0, iw = 0, arr =[];
        for(ih = 0; ih < this.height; ih++ ) {
            for(iw = 0; iw < this.width; iw++ ) {
                //if(posx !== this.width - 1  && posy !== ghmo && posx !== 0 && posy !== 0)*/)
                arr.push({alive : this.initialCellState , pos : {x:iw , y:ih}, nbOfCellsAliveNearMe:this.nbOfcellsAliveNearMe});
            }
        }
        return arr;
    }

    generateFrameAtPos(){
        let x = Math.floor(Math.random()*this.width-1), y = Math.floor(Math.random()*this.height-1),
        setOfFrame = [
            [{x:x-1,y:y},{x:x,y:y},{x:x+1,y:y}],
            [{x:x-2,y:y},{x:x-1,y:y},{x:x,y:y},{x:x+1,y:y}],
            [{x:x-2,y:y},{x:x-1,y:y},{x:x,y:y},{x:x+1,y:y},{x:x+2,y:y}],
            [{x:x + 3,y:y},{x:x + 2,y:y},{x:x + 1,y:y},{x:x,y:y-1},{x:x,y:y},{x:x-1,y:y}],
            [{x:x-1,y:y+1},{x:x,y:y+1},{x:x,y:y},{x:x+1,y:y},{x:x+1,y:y-1},{x:x+2,y:y-1}],
            [{x:x,y:y+1},{x:x-1,y:y},{x:x,y:y},{x:x,y:y-1},{x:x+1,y:y-1}]
          ];
          
        return setOfFrame;
    }

    getWidth(){
        return this.width;
    }
    getHeight(){
        return this.height;
    }
    getContent(){
        return this.content;
    }
    setContent(content){
        this.content = content;
    }
}